/***********************
WHERE MAGIC HAPPENS
**********************/

img.simplicity {
	max-width: 523px;
	margin-top: -26px;
	margin-right: 13px;
}

.follow-us {
	height: 30px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
}
.follow-us p {
	display: inline-block;
	color: #939393;
	font-family: "Raleway","Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.social-icon {
	display: inline-block;
	max-height: 100%;
	width: 26px;
	height:26px;
	margin-left: 5px;
	position: relative;
	-webkit-transition: background 0.3s ease-in-out;
	-moz-transition: background 0.3s ease-in-out;
	-ms-transition: background 0.3s ease-in-out;
	-o-transition: background 0.3s ease-in-out;
	transition: background 0.3s ease-in-out;
	-webkit-backface-visibility: hidden;
	-webkit-transform: translateZ(0) scale(1.0, 1.0);
}

.social-icon:after {
	content: "";
	opacity: 0;
	display:block;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	-moz-transition: all .4s ease;
	-webkit-transition: all .4s ease;
	-ms-transition: all .4s ease;
	-o-transition: all .4s ease;
	transition: all .4s ease;
}
.social-icon:hover:after {
	opacity: 1;
}

div.social-icon.face {
	background: url("../images/icons/FB-icon.svg") 0 0 no-repeat transparent;
}
div.social-icon.face:after {
	background: url("../images/icons/FB-icon-hover.svg") 0 0 no-repeat transparent;
}
div.social-icon.gplus {
	background: url("../images/icons/G+-icon.svg") 0 0 no-repeat transparent;
}
div.social-icon.gplus:after {
	background: url("../images/icons/G+-icon-hover.svg") 0 0 no-repeat transparent;
}
div.social-icon.twit {
	background: url("../images/icons/TWITTER-icon.svg") 0 0 no-repeat transparent;
}
div.social-icon.twit:after {
	background: url("../images/icons/TWITTER-icon-hover.svg") 0 0 no-repeat transparent;
}
div.social-icon.inst {
	background: url("../images/icons/INSTAGRAM-icon.svg") 0 0 no-repeat transparent;
}
div.social-icon.inst:after {
	background: url("../images/icons/INSTAGRAM-icon-hover.svg") 0 0 no-repeat transparent;
}

ul.slides li.overlay:before {
	content: initial;
}

.vid-bg,
ul.slides,
.image-slider .slides {
	background: transparent;
}
